import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { Device } from '@ionic-native/device';
import { NetworkInterface } from '@ionic-native/network-interface';
import { Geolocation } from '@ionic-native/geolocation';
import { Network } from '@ionic-native/network';
import { HTTP } from '@ionic-native/http';

/**
 * Generated class for the EquipoFrecuentePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-equipo-frecuente',
  templateUrl: 'equipo-frecuente.html',
})
export class EquipoFrecuentePage {

  public id: string;
  public model?: string;
  public cordova?: string;
  public platform?: string;
  public version?: string;
  public manufacturer?: string;
  public serial?: string;
  public isVirtual?: boolean;
  public lat?: number;
  public long?: number;
  

  constructor(public navCtrl: NavController, public navParams: NavParams, private device: Device, private http: HTTP,
              public networkInterface: NetworkInterface, private geolocation: Geolocation, private network: Network){

              this.id = this.device.uuid;
              this.model = this.device.model;
              this.cordova = this.device.cordova;
              this.platform = this.device.platform;
              this.version = this.device.version;
              this.manufacturer = this.device.manufacturer;
              this.serial = this.device.serial;
              this.isVirtual = this.device.isVirtual;              
              
  }

  getInfo() {
    

    alert("id: "+this.id +
          "\nmodel: "+this.model+
          "\ncordova: "+this.cordova+
          "\nplatform: "+this.platform+
          "\nversion: "+this.version+
          "\nmanufacturer: "+this.manufacturer+
          "\nserial: "+this.serial+
  "\nisVirtual: "+this.isVirtual);
  }
  
  getGeolocation(){
    this.geolocation.getCurrentPosition().then((resp) => {
        this.lat =  resp.coords.latitude;
        this.long =  resp.coords.longitude;
        alert("latitud: "+ this.lat + "\nlongitud: "+this.long);
     }).catch((error) => {
       //console.log('Error getting location', error);
       alert('Error getting location' + error);
     });
  }

}



